<script>
    import Vue from "vue"
    import $ from "jquery"
    export default {
        name: "SteelOnline",
        components: {},
        data() {
            return {
              activeName:['1','2','3'],
              Home:[
                {name:"111",ThisTrue:true},
                {name:"111",ThisTrue:false},
                {name:"111",ThisTrue:false},
              ],
              //数量
              quantity:"",
              quantityshow:true,
              //模拟的selet
              tableData:[{'name':111}, {'name':222}, {'name':333}, {'name':444}],
              selectValue:"12312123",
              //第二个
              sizeTableData:[{'name':111}, {'name':222}, {'name':333}, {'name':444}],
            }
        },
        methods: {
          //模拟selet
          // selectGetvalue(index,item){
          //   this.selectShow=false;
          // },
          //监听数量
          HearQuantity(){
            console.log(this.quantity);
            if(this.quantity!=""){
              this.quantityshow=true;
            }else{
              this.quantityshow=false
            }

          },
          //下一步跳转
          nextStep(){
            console.log("1");
            this.$router.push({
              path:'SmtParameter'
            })
          },
          Onclick(data,index){
            this.Home.forEach((item,index)=>{
              console.log(item);
              item.ThisTrue=false;
            });
            data.ThisTrue=true;
          },
        },
      mounted() {
        $(".SteelOnline").on("click",function (event) {     event.stopPropagation();   $(this).find(".selectNumber ul").hide(); });
        $(".selectNumber .input").on("click",function(event) {    event.stopPropagation();  $(".selectNumber").find(".selectNumber ul").hide() ; $(this).parents(".selectNumber").find(".list ul").show(); });
        $(".selectNumber li").on("click",function(event){   event.stopPropagation();  $(this).parents(".selectNumber").find(".input p").text($(this).text());  $(this).parents("ul").hide(); })
        $(".option p").click(function(){console.log("11");$(this).addClass("select");$(this).siblings().removeClass("select")});
        $(".opboxtion p").click(function(){if($(this).hasClass("select")){$(this).removeClass("select")}else{$(this).addClass("select");}})
      },
      created() {
          this.$emit('getMessage', "2");
        }
    }

</script>

